<!-- 在页面中添加一个按钮，点击按钮后获取用户信息 -->
<template>
	<view>
		<button @click="getUserInfo">获取用户信息</button>
		<button open-type="getPhoneNumber" @getphonenumber="aaa">获取用户信息</button>
		<open-data type="userNickName"></open-data>
		<!-- <open-data type="userAvatarUrl" style="width: 100px;height: 100px;"></open-data> -->
		<open-data type="userGender"></open-data>
		<button @click="getUserInfo">Profile</button>
		<button @click="ac">Profile12</button>
	</view>
</template>

<script>
	export default {
		data(){
			return{
				key:''
			}
		},
		methods: {
			getUserInfo() {
				// 调用 login 接口获取 code
				uni.login({
					provider: 'weixin',
					success: (res) => {
						
						// 发送 code 到后端，后端通过 code 获取 session_key 和 openid
						uni.request({
							url: 'https://1el9898253.oicp.vip/aaa/',
							method: 'POST',
							data: {
								code: res.code
							},
							success: (res) => {
								
								// 获取 session_key 和 openid 成功
								const sessionKey = res.data.session_key;
								const openid = res.data.openid;
								this.key=sessionKey
								console.log('ksy= ' + sessionKey);
								console.log('openid= ' + openid);
								
							}
						});
					}
				});
				
				this.getProfile()
			},

			aaa(e) {
				uni.authorize({
					scope: 'scope.phoneNumber',
					success: function() {
						// 用户已授权获取手机号码
						uni.getPhoneNumber({
							provider: 'weixin',
							success: function(res) {
								// 获取手机号码成功
								console.log(res)
							},
							fail: function(res) {
								// 获取手机号码失败
								console.log(res)
							}
						})
					},
					fail: function() {
						// 用户未授权获取手机号码
						console.log('未授权获取手机号码')
					}
				})


			},
		
			getProfile(){
				uni.getUserProfile({
					desc:'获取用户信息',
					success:(res)=>{
						// console.log(res)
						let encryptedData=res.encryptedData
						let iv=res.iv
						uni.request({
							url: 'https://1el9898253.oicp.vip/decrypt/',
							method: 'POST',
							data: {
								encryptedData: encryptedData,
								iv: iv,
								sessionKey: this.key
							},
							success: (res) => {
								// 解密手机号数据成功
								const phoneNumber = res.data.phoneNumber;
								console.log(res);
							}
						});
					}
				})
			},
			ac(){
				uni.authorize({
				    scope: 'scope.userInfo',
				    success() {
				        console.log(1231241)
				    }
				})
			}
		}
	}
</script>
